<template>

  <div>
    <div id="load"><img src="../assets/img/loading.gif" alt=""></div>
  </div>
</template>

<script>
    export default {
        name: "beforeHealthReport.vue",
      data() {
        return {
          msg: '',
          rep: '',
          menuList: '',
          Histories: ''
        }
      },
      created() {
        this.post();
      },
      computed: {
        api_url() {
          return this.$store.state.api_url
        },
        cusId() {
          return this.$store.state.cusId
        },

      },
      mounted(){
        this.getdata();
      },
      methods: {
        post() {
          this.axios.post(this.api_url + '/security/getToken/zhufuAdmin/zhufuAdmin@2019chni', {
            data: {
              key: "",
              requestTime: ""
            }
          })
          //成功返回
            .then(response => {
              console.log(response.data);
              this.msg = response.data;
            })
            //失败返回
            .catch(error => {
              console.log(error)
            })
        },
        /**
         *
         * 健康报告接口
         *
         * */
        getdata() {

          document.getElementById('load').style.display='block';
          this.axios.get(this.api_url + '/report/getPerotList/' + this.$route.params.cusId, {headers: {'Authorization': this.msg.data}})
          //成功返回
            .then(response => {
              console.log(response.data);
              this.rep = response.data;

              if (this.rep.code === '0002') {
                this.post();
                this.getdata();
              } else {
                this.$store.commit('Myrep',this.rep);
                console.log('$store:'+this.$store.state.Myrep)
                this.$destroy('mainMenu');
                this.$router.push({path: '/HealthReport'});

              }


            })
            //失败返回
            .catch(error => {
              console.log(error)
            });

        },

      }
    }
</script>

<style scoped>

  #load{

    display: block;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color:rgba(255,255,255,0.5) ;
  }
  #load img{
    opacity:0.4;
    margin-top: 40%;
    width: 100%;
  }
</style>



