<template>
  <div class="container">
    <div class="header" @click="back"><img src="../assets/img/pin_Left.png" alt="">健康报告</div>
    <div style="height: 40px"></div>
    <div class="card" v-for="(item,index) in this.msg.data">
      <div class="item1">
        <div class="Sername">{{item.name}}</div>
        <div class="Serdate">{{item.date}}</div>
      </div>
      <div class="item2">
        距离随访日还有 <span>{{item.days}}</span> 天
      </div>
      <div class="item3">
      </div>
    </div>

  </div>
</template>

<script>
  export default {
    name: "healthSer",
    computed: {
      msg() {
        return this.$store.state.serlist
      }
    },
    methods:{
      back() {
        this.$destroy('healthSer')
        this.$router.go(-1);

      },

    }
  }
</script>

<style scoped>
  .card {
    margin: 10px;
    display: flex;
    border: .5px solid #6296ca;
    flex-wrap: wrap;
    border-radius: 6px;
    background-color: white;
  }

  .item1 {
    flex: 0 0 50%;
    /*background-color: cornflowerblue;*/
    padding-left: 10px;
  }

  .item1 .Sername {
    display: flex;
    justify-content: flex-start;
    margin: 10px 0;
    font-size: 18px;
    font-weight: 600;
  }

  .item1 .Serdate {
    display: flex;
    justify-content: flex-start;

  }

  .item2 span {
    color: #6495ed;

  }

  .item2 {
    display: flex;
    flex: 0 0 50%;
    justify-content: flex-end;
    align-items: flex-end;
    padding-right: 10px;
  }

  .item3 {
    margin-top: 10px;
    border-radius: 5px;
    display: flex;
    min-height: 10px;
    min-width: 100%;
    background-color: #6495ed;
  }

</style>



