<template>
  <div class="container">
    <div class="header" @click="back"><img src="../assets/img/pin_Left.png" alt="">健康报告</div>
    <div style="height: 30px"></div>
    <div class="report">
      <table>
        <tbody>
        <tr v-for="(item,index) in this.rep.data">
          <td @click="openRep(item.url)"><img class="iconImg" :src=item.img>
          </td>
          <td class="title" @click="openRep(item.url)">{{item.name}}</td>
          <td class="Border" style="border-color: #4584E5" @click="openRep(item.url)">
            <p>{{item.year}}</p>
            <p>{{item.date}}</p>
          </td>
        </tr>
        </tbody>

        <!--<tr @click="mental">
          <td><img src="../assets/img/icon-juzi.png">
          </td>
          <td class="title">心理评估测评</td>
          <td style="border-color: #73E4F2">
            <p>2018年</p>
            <p>8月12日</p>
          </td>
        </tr>
        <tr @click="constit">
          <td><img src="../assets/img/icon-zhongyi.png">
          </td>
          <td class="title">中医体质辨识报告</td>
          <td style="border-color: #feb43d">
            <p>2018年</p>
            <p>8月12日</p>
          </td>
        </tr>-->
      </table>
    </div>
  </div>
</template>

<script>
  export default {
    name: "HealthReport",
    data() {
      return {
        msg: '',
        rep:JSON.parse(sessionStorage.getItem("Myrep"))
      }
    },
    computed:{
      api_url(){
        return this.$store.state.api_url
      },
    },
    mounted(){
      var arr1 = new Array(this.rep.data.length);
      for(var i=0;i<arr1.length;i++){
        arr1[i] = i;
      }

      for (i = 0; i < arr1.length; i++) {
        var Id=arr1[i];
          document.getElementsByClassName('Border')[Id].style.borderColor=this.rep.data[i].color;
          // document.getElementsByClassName('iconImg')[Id].src='../assets/img/'+this.rep.data[i].img+'.png';
      }
    },
    methods:{
      back() {
        this.$router.go(-1);
        sessionStorage.removeItem("Myrep");
        this.$destroy('HealthReport')
      },
      openRep(url){
        console.log('打开报告，马上给我安排');
        console.log(url);
        this.$store.commit('repurl',url);
        console.log(this.$store.state.repurl);
        this.$router.push({path:'/reportCon'});
      },

    }
  }
</script>

<style scoped>


  .report {
    margin: 10px;
    background-color: #f7f8f8;

  }

  .report table {
    width: 100%;
    border-collapse: separate;
    border-spacing: 0 10px;
  }
  .report tr td {
    background-color: white;
    height: 80px;

  }

  .report tr td:last-child {
    border-right: 10px solid;
    border-radius: 0 5px 5px 0;

  }

  .report tr td:first-child {
    border-radius: 5px 0 0 5px;

  }

  .report tr td img {
    width: 40px;
    margin-left: 10px;
  }
  .report .title {
    font-size: 16px;
  }
</style>



