<template>
  <div>
    这是套餐介绍
    <!--<button @click="gethis">历史指标</button>-->
    <button @click="getdata">健康报告</button>
    <button @click="getmenu">健康档案首页菜单</button>


    <p style="color: red">{{rep.msg}}</p>
  </div>


</template>

<script>
  export default {
    name: "PlansIntroduce",
    data() {
      return {
        msg: '',
        rep:'',
        menuList:'',
        Histories:''
      }
    },
    created() {
      this.post();
    },
    computed:{
      api_url(){
        return this.$store.state.api_url
      },
      cusId(){
        return this.$store.state.cusId
      }
    },
    methods:{
      post(){
        this.axios.post(this.api_url+'/security/getToken/zhufuAdmin/zhufuAdmin@2019chni', {
          data: {
            key: "",
            requestTime: ""
          }
        })
        //成功返回
          .then(response => {
            console.log(response.data);
            this.msg = response.data;
          })
          //失败返回
          .catch(error => {
            console.log(error)
          })
      },
      /**
       *
       * 健康报告接口
       *
       * */
      getdata(){
        console.log(this.cusId);
        this.axios.get(this.api_url+'/report/getPerotList/'+this.cusId,{headers:{'Authorization':this.msg.data}})
        //成功返回
          .then(response => {
            console.log(response.data);
            this.rep=response.data;
            /*将请求的数据存入本地sessionStorage中*/
            if(!sessionStorage.getItem("rep")){
              /*如果存在就不用存了，避免重复点击*/
              sessionStorage.setItem("rep",JSON.stringify(this.rep));
              this.getdata();
            }else {
              if(this.rep.code==='0002'){
                this.post();
                this.getdata();
              }else {
                this.$router.push({path:'/HealthReport'});
              }
            }


          })
          //失败返回
          .catch(error => {
            console.log(error)
          });

      },
      /**
       *
       * 健康档案首页菜单
       *
       * */
      getmenu(){
        console.log(this.cusId);
        this.axios.post(this.api_url+'/tjZhufuhealth/getHealthIndex/'+this.cusId)
        //成功返回
          .then(response => {
            console.log(response.data);
            this.menuList = response.data;
            /*将请求的数据存入本地sessionStorage中*/
            if(!sessionStorage.getItem("menuList")){
              /*如果存在就不用存了，避免重复点击*/
              sessionStorage.setItem("menuList",JSON.stringify(this.menuList));
              this.getmenu();
            }else {
              if(this.rep.code==="0002"){
                this.post();
                this.getmenu();
              }else {
                this.$router.push({path:'/Menu'});
              }
            }


          })
          //失败返回
          .catch(error => {
            console.log(error)
          })
      },
    }

  }
</script>

<style scoped>

</style>



