<template>
  <div>
    <div class="header" @click="back"><img src="../assets/img/pin_Left.png" alt="">历史指标</div>
    <div style="height: 40px"></div>
    <div class="echarts">
      <div id="backImg">
        <div id="box"></div>
      </div>
    </div>
    <div style="height: 210px"></div>
    <table width="100%" class="table">
      <tbody>
      <tr>
        <td>
          检测时间
        </td>
        <td>
          检测值
        </td>
      </tr>
      <tr v-for="(item,index) in msg.data">
        <td>
          {{item.checkitemTimeTemp || '暂无数据'}}
        </td>
        <td>
          {{item.checkitemValue || '--'}}{{item.ckeckUtil}}
        </td>
      </tr>
      </tbody>
    </table>
    <!--<div v-for="(item,index) in msg.data">
      <div class="list">{{item}}
        <div class="icon"></div>
        <span>{{item.checkitemTimeTemp}}{{item.orgCheckitemName}} </span></div>
    </div>-->
  </div>
</template>

<script>
  export default {
    name: "Histories",
    data() {
      return {
        // msg: JSON.parse(sessionStorage.getItem("Histories")),
        Xdata:'',
        Ydata:''
      }
    },
    computed: {
      msg() {
        return this.$store.state.Histories
      }
    },

    mounted() {
      this.Xdata=new Array(this.msg.data.length);
      this.Ydata=new Array(this.msg.data.length);
      for(var i=0;i<this.Xdata.length;i++){
        if(this.msg.data[i].checkitemTimeTemp==null){
          this.Xdata[i] = '暂无数据';
        }else{
          this.Xdata[i] = this.msg.data[i].checkitemTimeTemp;
        }

        this.Ydata[i] = parseInt(this.msg.data[i].checkitemValue);
      };
      console.log(this.Xdata);
      this.drawLine();

    },
    methods: {
      back() {
        this.$router.go(-1);
        this.$destroy('Histories')
      },
      drawLine() {
        // 基于准备好的dom，初始化echarts实例
        let myChart = this.$echarts.init(document.getElementById('box'))
        // 绘制图表
        myChart.setOption({
          tooltip: {
            trigger: 'axis',
            position: function (pt) {
              return [pt[0], '100%'];
            }
          },
          // 标题
          title: {
            text: this.msg.data[0].orgCheckitemName+'（单位：'+this.msg.data[0].ckeckUtil+'）',
            left: 'center',
            textStyle: {
              //文字颜色
              color: '#FFFFFF',
              //字体风格,'normal','italic','oblique'
              fontStyle: 'normal',
              //字体粗细 'normal','bold','bolder','lighter',100 | 200 | 300 | 400...
              fontWeight: 'bold',
              //字体系列
              fontFamily: '微软雅黑',
              //字体大小
              fontSize: 15,

            }

          },
          grid : {

            y:'15%',
            x:'15%',
            x2:'15%',
            y2:'20%'
          },

          xAxis: {
            type: 'category',
            axisTick: {
              show: false
            },
            boundaryGap: false,
            //坐标轴颜色
            data: this.Xdata,
            splitLine: {
              show: false
            }, //去除网格线
            //坐标轴颜色
            axisLine: {
              lineStyle: {
                color: '#FFFFFF'
              }
            },



          },
          yAxis: {
            type: 'value',
            boundaryGap: [0, '10%'],
            type: 'value',
            axisTick: {
              show: false
            },
            splitLine: {
              show: false
            }, //去除网格线
            axisLine: {
              lineStyle: {
                color: '#FFFFFF',
              }
            },

          },
          dataZoom: [{
            type: 'inside',
            start: 0,
            end: 50,
            moveOnMouseMove:true,
            zoomOnMouseWheel:true,
          }],
          series: [
            {
              name: this.msg.data[0].orgCheckitemName,
              type: 'line',
              symbol: 'circle',
              symbolSize: 8,
              itemStyle: {
                normal: {
                  color: '#86C1FD',
                  borderColor: '#FFFFFF', //拐点边框颜色
                }
              },

              data: this.Ydata/*[82, 62, 100, 134, 90, 82, 62, 100, 134, 90, 82, 62, 100, 134, 90]*/
            }
          ]
        });
      }
    },

  };

</script>

<style scoped>
  .echarts {
    width: 100%;
    position: fixed;
    left: 3%;
    text-align: center;
    background-color: #ffffff;
  }

  #backImg {
    margin-top: 10px;
    background: url('../assets/img/bgcharts.jpg');
    -moz-background-size: 100% 100%;
    background-size: 100% 100%;
    text-align: center;
    width: 94%;
  }

  #box {
    width: 100%;
    height: 200px;
  }
  table {
    margin-top: 20px;
    background-color: #FFFFFF;
  }

  .table tbody td,
  .table tbody th {
    border-bottom: 1px solid #D8D8D8;
    height: 35px;
    text-align: center;
  }

  .table thead td,
  .table thead th {
    border-bottom: 1px solid #D8D8D8;
    height: 35px;
    text-align: center;
  }
</style>



