<template>
  <div class="container">
    <div class="header" @click="back"><img src="../assets/img/pin_Left.png" alt="">健康报告</div>
    <div style="height: 40px"></div>
    <table class="self-card">
      <tr>
        <td>
          <span>个人信息</span>
          <img :src=this.msg.data.headImg width="80px">
        </td>
        <td class="ifo">
          <p>姓名：{{this.msg.data.name}}</p>
          <p>性别：{{this.msg.data.sex}}</p>
          <p>年龄：{{this.msg.data.age}}</p>
        </td>
      </tr>
    </table>
    <div class="tab">健康指标</div>
      <div v-for="(item,index) in this.msg.data.typeList">
        <div class="list" @click="gethis(item.id)"><div class="icon"></div><span>{{item.name}}</span></div>
      </div>

    <!--<div v-for="(item,index) in msg">
      <div class="title" @click="details(index)">{{item.title}}</div>
    </div>-->
  </div>
</template>

<script>
  export default {
    name: "Menu",
    data() {
      return {

        address: '',
        cusid:JSON.parse(sessionStorage.getItem("cusid")),

      }
    },
    mounted() {
      console.log(this.cusid)
      /*给信息栏的icon图标设置颜色*/
      var arr1 = new Array(this.msg.data.typeList.length);
      for(var i=0;i<arr1.length;i++){
        arr1[i] = i;
      }

      for (i = 0; i < arr1.length; i++) {
        var Id=arr1[i];

        if(Id%4===0){
          document.getElementsByClassName('icon')[Id].style.backgroundColor='rgb(144, 198, 255)'
        }else if(Id%4===1){
          document.getElementsByClassName('icon')[Id].style.backgroundColor='rgb(255, 167, 131)'

        }else if(Id%4===2){
          document.getElementsByClassName('icon')[Id].style.backgroundColor='rgb(119, 214, 223)'

        }else if(Id%4===3){
          document.getElementsByClassName('icon')[Id].style.backgroundColor='rgb(255, 186, 105)'

        }

      }

    },

    computed:{
      api_url(){
        return this.$store.state.api_url
      },
      msg(){
        return this.$store.state.Myifo
      }

    },
    created(){
      this.axios.post(this.api_url+'/security/getToken/zhufuAdmin/zhufuAdmin@2019chni', {
        data: {
          key: "",
          requestTime: ""
        }
      })
      //成功返回
        .then(response => {

          this.address = response.data;

        })
        //失败返回
        .catch(error => {
          console.log(error)
        })
    },
    methods:{
      back() {
        this.$router.go(-1);
      },
      post(){
        this.axios.post(this.api_url+'/security/getToken/zhufuAdmin/zhufuAdmin@2019chni', {
          data: {
            key: "",
            requestTime: ""
          }
        })
        //成功返回
          .then(response => {
            this.address = response.data;
          })
          //失败返回
          .catch(error => {
            console.log(error)
          })
      },
      /**
       *
       * 历史指标接口
       *
       **/
      gethis(id){
        console.log(this.address.data+id+this.cusid);
        this.axios.post(this.api_url+'/tjZhufuhealth/selectBycheck/'+this.cusid+'/'+id,
          {
            data: {1:1},
          },
          {
            headers :{
              'Authorization':this.address.data
            }
          })
        //成功返回
          .then(response => {
            console.log(response.data)
            this.Histories = response.data;
              if(this.Histories.code==="0002"){
                this.post();
                this.gethis();
              }else {
                this.$router.push({path:'/Histories'});
                this.$store.commit('Histories',this.Histories)
              }
          })
          //失败返回
          .catch(error => {
            console.log(error)
          })
      }

    }
  }
</script>

<style scoped>
  .self-card {
    background-color: #ffffff;
    text-align: left;
    margin: 10px;
    border-bottom: 1px solid #cccccc;
  }

  .self-card tr td{
    padding: 10px;
  }
  .self-card span {
    display: block;
    margin-bottom: 5px;
  }


  .self-card p {
    margin-left: 10px;
  }

  .self-card .ifo {
    width: 80%;
    /*background-color: antiquewhite;*/
  }
  .tab{
    background-color: #ffffff;
    height: 40px;
    line-height: 40px;
    border-bottom: 1px solid #cccccc;
  }
  .list{
    background-color: #ffffff;
    text-align: left;
    height: 40px;
    line-height: 40px;
    border-bottom: 1px solid #cccccc;
  }
  .list span{
    margin-left: 20px;
    font-weight: 600;
  }
  .icon{
    display: inline-block;
    background-color: red;
    min-height: 20px;
    min-width: 10px;
    border-radius:5px;
    margin-left: 10px;
    position: relative;
    top:5px;

  }
</style>



