<template>
  <div>
    <div class="menu-card" @click="getmenu"><img src="../assets/img/information.png"></div>
    <div class="menu-card" @click="getdata"><img src="../assets/img/report.png"></div>
    <div class="menu-card" @click="getlist"><img src="../assets/img/shebei.png"></div>
    <div id="load"><img src="../assets/img/loading.gif" alt=""></div>
  </div>
</template>

<script>
  export default {
    name: "mainMenu",
    data() {
      return {
        msg: '',
        rep: '',
        menuList: '',
        Histories: '',
        serList:'',
      }
    },
    created() {
      this.post();
    },
    computed: {
      api_url() {
        return this.$store.state.api_url
      },
      cusId() {
        return this.$store.state.cusId
      },

    },
    methods: {
      post() {
        this.axios.post(this.api_url + '/security/getToken/zhufuAdmin/zhufuAdmin@2019chni', {
          data: {
            key: "",
            requestTime: ""
          }
        })
        //成功返回
          .then(response => {
            console.log(response.data);
            this.msg = response.data;
          })
          //失败返回
          .catch(error => {
            console.log(error)
          })
      },
      /**
       *
       * 健康报告接口
       *
       * */
      getdata() {

        document.getElementById('load').style.display = 'block';
        this.axios.get(this.api_url + '/report/getPerotList/' + this.$route.params.cusId, {headers: {'Authorization': this.msg.data}})
        //成功返回
          .then(response => {
            console.log(response.data);
            this.rep = response.data;

            if (this.rep.code === '0002') {
              this.post();
              this.getdata();
            } else {
              this.$store.commit('Myrep', this.rep);
              sessionStorage.setItem("Myrep", JSON.stringify(this.rep));
              this.$destroy('mainMenu');
              this.$router.push({path: '/HealthReport'});

            }


          })
          //失败返回
          .catch(error => {
            console.log(error)
          });

      },
      /**
       *
       * 健康档案首页菜单
       *
       * */
      getmenu() {

        document.getElementById('load').style.display = 'block';
        this.axios.post(this.api_url + '/tjZhufuhealth/getHealthIndex/' + this.$route.params.cusId)
        //成功返回
          .then(response => {
            console.log(response.data);
            this.menuList = response.data;

            if (this.menuList.code === "0002") {
              this.post();
              this.getmenu();
            } else {
              sessionStorage.setItem("cusid", JSON.stringify(this.$route.params.cusId));
              this.$store.commit('Myifo', this.menuList);
              console.log('$store:' + this.$store.state.Myifo)
              this.$destroy('mainMenu');
              this.$router.push({path: '/Menu'});
            }


          })
          //失败返回
          .catch(error => {
            console.log(error)
          })
      },
      /**
       *
       * 服务列表接口
       *
       * */

    getlist() {
        // document.getElementById('load').style.display='block';
        console.log(this.$route.params.cusId)
        document.getElementById('load').style.display = 'block';
        this.axios.post('http://192.168.1.129/'+ 'service/getHealthServiceList/0',{
            "cusId": this.$route.params.cusId,
            "openid": "",
          headers: {'Authorization': this.msg.data}
        })
        //成功返回
          .then(response => {
            console.log(response.data)
            this.serList = response.data;
            if (this.serList.code === "0002") {
              this.post();
              this.getlist();
            } else {
              sessionStorage.setItem("cusid", JSON.stringify(this.$route.params.cusId));
              this.$store.commit('serlist', this.serList);
              this.$destroy('mainMenu');
              this.$router.push({path: '/healthSer'});
            }

          })
          //失败返回
          .catch(error => {
            console.log(error)
          });
/*
        this.$.ajax({
          type: "POST",
          url: 'http://192.168.1.129/'+ 'service/getHealthServiceList/0',
          contentType: "application/json;charset=utf-8",
          dataType: 'json',
          data: JSON.stringify(dataJson),
          /!*headers:{Authorization:this.msg.data},*!/
          /!*Authorization:this.msg.data,*!/
          success: function(msg){
            this.serList = msg.data;
            if (this.serList.code === "0002") {
              this.post();
              this.getlist();
            } else {
              sessionStorage.setItem("cusid", JSON.stringify(this.$route.params.cusId));
              this.$store.commit('serlist', this.serList);
              console.log('$store:' + this.$store.state.serlist)
              this.$destroy('mainMenu');
              this.$router.push({path: '/healthSer'});
            }
          },
          error:function (err) {
            console.log(err)

          }
        })*/

      }
    }
  }
</script>

<style scoped>
  .menu-card {
    margin: 5px 10px;
  }

  .menu-card img {
    width: 100%;
  }

  #load {

    display: none;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: rgba(255, 255, 255, 0.5);
  }

  #load img {
    opacity: 0.4;
    margin-top: 40%;
    width: 100%;
  }
</style>



