import Vue from 'vue'
import Router from 'vue-router'
import zhu from '@/components/zhu'
import companyProfile from '@/components/companyProfile'
import Consulting from '@/components/Consulting'
import equipmentIntroduction from '@/components/equipmentIntroduction'
import importantNotice from '@/components/importantNotice'
import PlansIntroduce from '@/components/PlansIntroduce'
import Details from '@/components/Details'
import HealthReport from  '@/components/HealthReport'
import Menu from '@/components/Menu'
import Histories from '@/components/Histories'
import mainMenu from '@/components/mainMenu'
import beforeMenu from '@/components/beforeMenu'
import beforeHealthReport from '@/components/beforeHealthReport'
import reportCon from '@/components/reportCon'
import healthSer from '@/components/healthSer'
import beforeHeslthService from '@/components/beforeHeslthService'



Vue.use(Router);


export default new Router({
  routes: [
    {
      path: '/',
      redirect:'/mainMenu/020000002883'
    },
    {
      path: '/Details/:index',
      name:'/Details',
      component:Details
    },
    {
      path: '/beforeHeslthService/:cusId',
      name:'/beforeHeslthService',
      component:beforeHeslthService
    },
    {
      path: '/beforeHealthReport/:cusId',
      name:'/beforeHealthReport',
      component:beforeHealthReport
    },
    {
      path: '/healthSer',
      name:'/healthSer',
      component:healthSer
    },
    {
      path: '/reportCon',
      name:'/reportCon',
      component:reportCon
    },
    {
      path: '/beforeMenu/:cusId',
      name:'/beforeMenu',
      component:beforeMenu
    },
    {
      path: '/mainMenu/:cusId',
      name:'/mainMenu',
      component:mainMenu
    },

    {
      path: '/HealthReport',
      name:'/HealthReport',
      component:HealthReport
    },
    {
      path: '/Menu',
      name:'/Menu',
      component:Menu
    },
    {
      path: '/Histories',
      name:'/Histories',
      component:Histories
    },
    {
      path: '/zhu',
      name: 'zhu',
      component: zhu,
      children:[{
        path: '/zhu/companyProfile',
        name: 'companyProfile',
        component: companyProfile
      },
        {
          path: '/zhu/Consulting',
          name: 'Consulting',
          component: Consulting
        },
        {
          path: '/zhu/equipmentIntroduction',
          name: 'equipmentIntroduction',
          component: equipmentIntroduction
        },
        {
          path: '/zhu/importantNotice',
          name: 'importantNotice',
          component: importantNotice
        },{
          path: '/zhu/PlansIntroduce',
          name: 'PlansIntroduce',
          component: PlansIntroduce
        }]
    },


  ]
})



