<template>
<div>
  <div class="header" @click="back"><img src="../assets/img/pin_Left.png" alt="">我的报告</div>
  <div style="height: 30px;"></div>
  <iframe height=100%
          :src=this.$store.state.repurl>
    <p>您的浏览器不支持  iframe 标签。</p>
  </iframe>
</div>
</template>

<script>
    export default {
        name: "reportCon",
      methods:{
        back() {
          this.$router.go(-1);
        },
      }

    }
</script>

<style scoped>
iframe{
  position: fixed;
  margin-top: 40px;
  top: 0;
  bottom: 0;
  left: 0;
  width: 100%;

}

</style>



